.CreateTodoButton {
  background-color: #61dafa;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;
  z-index: 1;

  color: #215e65;
  box-shadow: 0px 0px 50px rgb(107, 193, 255, 0.3);

  transition: 0.3s ease;
}

.CreateTodoButton:hover {
  background-color: #00fff1;
  text-shadow: 0 0 5px #00fff1;
  text-shadow: 0 0 20px #00fff1;
}
