form {
  width: 90%;
  max-width: 300px;
  background-color: #091921;
  padding: 33px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
label {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  color: #a5a5a5;
  margin-bottom: 26px;
}
textarea {
  background-color: #091921;
  border: 2px solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #215e65;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
}
textarea::placeholder {
  color: #a5a5a5;
  font-weight: 400;
}
textarea:focus {
  outline: none !important;
  border-color: #719ece;
  box-shadow: 0 0 10px #719ece;
  outline-color: #61dafa;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button:hover {
  color: #00fff1;
  text-shadow: 0 0 5px #00fff1;
  text-shadow: 0 0 20px #00fff1;
}

.TodoForm-button {
  background: #091921;
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #215e65;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
}
