.TodoItem {
  background-color: #091921;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 0px 50px rgb(107, 193, 255, 0.3);
}

.TodoItem-p {
  color: #215e65;
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  color: #215e65;
  text-decoration: line-through;
}
.btn-radio {
  appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
}
.fas {
  position: relative;
  width: 40px;
  height: 40px;
  background: #091921;
  line-height: 40px;
  color: #6f6f6f;
  font-size: 16px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1),
    2px 2px 6px rgba(0, 0, 0, 0.8);
}
.fas:hover {
  box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1),
    2px 2px 6px rgba(0, 0, 0, 0.8),
    inset -2px -2px 10px rgba(255, 255, 255, 0.05),
    inset 2px 2px 10px rgba(0, 0, 0, 0.5);
}
.btn-radio:checked ~ .fas {
  color: #00fff1;
  box-shadow: inset -1px -1px 3px rgba(255, 255, 255, 0.1),
    inset 2px 2px 6px rgba(0, 0, 0, 0.8);
  text-shadow: 0 0 5px #00fff1;
  text-shadow: 0 0 20px #00fff1;
}
